












































































































import { Vue, Component } from 'vue-property-decorator'
import Pagelayout from '@/components/Pagelayout.vue'
import OperationDialog from './components/OperationDialog.vue'
import { getEndadressList,deleteEndadress } from '@/api/endAdressManage'
@Component({
  components: {
    Pagelayout,
    OperationDialog
  }
})
export default class Ordermanage extends Vue {

  pagination = {
    limit: 20,
    page: 1
  }

  queryForm = {
    end_name: ''
  }

  tableData = []
  total = 0

  created() {
    this.loadData()
  }

  loadData() {
    this.getlist()
  }

  async getlist() {
    const { result:{ res, count } } = await getEndadressList(Object.assign({}, this.pagination, this.queryForm))
    this.tableData = res
    this.total = count
  }

  handlePageChange(page: number) {
    this.pagination.page  = page
  }

  handleSizeChange(pageSize: number) {
    this.pagination.page = 1
    this.pagination.limit = pageSize
    this.getlist()
  }

  handleSearch() {
    this.getlist()
  }

  handleReset() {
    this.pagination.page = 1
    this.pagination.limit = 20
    this.queryForm = { end_name: '' }
    this.getlist()
  }

  handleAdd () {
    (this.$refs.OperationDialogDom as OperationDialog).handlOpen()
  }

  handleEdite (item: { end_name: string,
      end_p_name:string,
      end_p_phone: string }) {
    (this.$refs.OperationDialogDom as OperationDialog).handlOpen(item)
  }

  handleDelete (id: string|number) {
    this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(async () => {
      const { code, message } = await deleteEndadress({id:JSON.stringify([id])})
      if(code === 1) {
        this.$message({
          type: 'success',
          message: message || '删除成功!'
        })
        this.resetDelete()
        this.getlist()
      } else {
        this.$message({
          type: 'error',
          message: message || '删除失败'
        })
      }

    }).catch(() => {
      this.$message({
        type: 'info',
        message: '已取消删除'
      })
    })
  }
  resetDelete(){
    const deleteNum = 1
    const totalPage = Math.ceil((this.total-deleteNum)/this.pagination.limit)
    this.pagination.page = this.pagination.page>totalPage?totalPage:this.pagination.page
    this.pagination.page = this.pagination.page<1?1:this.pagination.page
  }
}



